
movie {

  .poster {

    position: relative;
    margin-bottom: 15px;

    &:before {
      display: block;
      content: "";
      width: 100%;
      padding-top: 150%;
    }

    > .innards {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 100%;
      background: #f9f9f9;
      border: 1px solid #ececec;
      border-radius: 6px;
    }

    .others {
      position: absolute;
      width: 0;
      height: 0;
    }

    img {
      width: 100%;
      border-radius: 6px;
    }

    .flipper-nav {

      display: block;
      position: absolute;

      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      a {
        position: absolute;
        display: block;
        top: 0;
        bottom: 0;
        width: 65px;
        height: 100%;
        padding: 0 10px;
        color: white;
        opacity: 0;

        &:hover,
        &:focus,
        &:active {
          opacity: .5;
        }

        &.prev {
          left: 0;
        }

        &.next {
          right: 0;
        }

        i {
          position: absolute;
          width: 42px;
          height: 45px;
          top: 50%;
          left: 50%;
          margin-top: -23px;
          margin-left: -21px;
        }

      }

    }

  }

}
